.header_links{position:relative;display:flex;align-items:center;justify-content:space-between;gap:30px}@media(max-width: 490px){.header_links{gap:20px}}.header_links a{text-decoration:none}.header_links a span{position:absolute;top:20px;left:13px;text-align:center;border-radius:50%;background-color:#e6007e;width:20px;font-size:14px}.header_links img[alt=корзина]{width:50px;height:50px}.header_links img[alt=профиль]{width:35px;height:35px}.header_links img[alt=меню]{width:35px;height:35px;cursor:pointer}